"use client"

import { useState, useEffect } from "react"
import { User, Mail, LogOut, Edit2, Camera, Award, Calendar as CalendarIcon } from "lucide-react"
import { useRouter } from "next/navigation"
import { motion, AnimatePresence } from "framer-motion"
import toast from "react-hot-toast"

interface UserData {
  id: string
  email: string
  name: string
  createdAt: string
  bio?: string
  timezone?: string
  workStartTime?: string
  workEndTime?: string
}

export default function ProfilePage() {
  const router = useRouter()
  const [user, setUser] = useState<UserData | null>(null)
  const [isEditing, setIsEditing] = useState(false)
  const [bio, setBio] = useState("")
  const [timezone, setTimezone] = useState("UTC")
  const [workStartTime, setWorkStartTime] = useState("09:00")
  const [workEndTime, setWorkEndTime] = useState("17:00")

  useEffect(() => {
    const userData = localStorage.getItem("currentUser")
    if (userData) {
      const parsed = JSON.parse(userData)
      setUser(parsed)
      setBio(parsed.bio || "")
      setTimezone(parsed.timezone || "UTC")
      setWorkStartTime(parsed.workStartTime || "09:00")
      setWorkEndTime(parsed.workEndTime || "17:00")
    }
  }, [])

  const handleSaveProfile = () => {
    if (user) {
      const updated = {
        ...user,
        bio,
        timezone,
        workStartTime,
        workEndTime,
      }
      localStorage.setItem("currentUser", JSON.stringify(updated))
      setUser(updated)
      setIsEditing(false)
      toast.success("‚úÖ Profile updated successfully!")
    }
  }

  const handleLogout = () => {
    localStorage.removeItem("currentUser")
    toast.success("üëã See you soon!")
    setTimeout(() => router.push("/"), 500)
  }

  if (!user) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <motion.div
          animate={{ rotate: 360 }}
          transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
          className="w-12 h-12 border-4 border-primary border-t-transparent rounded-full"
        />
      </div>
    )
  }

  const memberDays = Math.floor(
    (new Date().getTime() - new Date(user.createdAt).getTime()) / (1000 * 60 * 60 * 24)
  )

  return (
    <div className="p-4 lg:p-8">
      {/* Header */}
      <motion.div 
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        className="mb-8"
      >
        <div className="flex items-center gap-3 mb-2">
          <motion.div
            animate={{ scale: [1, 1.1, 1] }}
            transition={{ duration: 2, repeat: Infinity }}
          >
            <User className="w-8 h-8 text-primary" />
          </motion.div>
          <h1 className="text-3xl font-bold text-foreground">Your Profile</h1>
        </div>
        <p className="text-muted-foreground">Manage your account and preferences</p>
      </motion.div>

      <div className="max-w-4xl space-y-6">
        {/* Profile Header Card */}
        <motion.div
          initial={{ opacity: 0, scale: 0.95 }}
          animate={{ opacity: 1, scale: 1 }}
          className="bg-gradient-to-br from-primary/10 via-secondary/10 to-accent/10 rounded-2xl shadow-xl p-8 border border-border"
        >
          <div className="flex flex-col lg:flex-row items-center gap-8">
            <div className="relative">
              <motion.div
                whileHover={{ scale: 1.05 }}
                className="w-32 h-32 bg-gradient-to-br from-primary to-secondary rounded-full flex items-center justify-center shadow-2xl relative"
              >
                <User className="w-16 h-16 text-primary-foreground" />
                <motion.button
                  whileHover={{ scale: 1.1 }}
                  whileTap={{ scale: 0.9 }}
                  className="absolute bottom-2 right-2 bg-background p-2 rounded-full shadow-lg border border-border"
                >
                  <Camera size={16} className="text-foreground" />
                </motion.button>
              </motion.div>
            </div>
            
            <div className="flex-1 text-center lg:text-left">
              <h2 className="text-3xl font-bold text-foreground mb-2">{user.name}</h2>
              <div className="flex flex-col lg:flex-row items-center gap-4 text-muted-foreground mb-4">
                <div className="flex items-center gap-2">
                  <Mail size={18} />
                  <span>{user.email}</span>
                </div>
                <div className="hidden lg:block w-1 h-1 rounded-full bg-muted-foreground" />
                <div className="flex items-center gap-2">
                  <Award size={18} />
                  <span>Member for {memberDays} days</span>
                </div>
              </div>
              <div className="flex flex-wrap gap-2 justify-center lg:justify-start">
                <span className="bg-primary/10 text-primary px-3 py-1 rounded-full text-sm font-medium border border-primary/20">
                  ‚≠ê Pro Member
                </span>
                <span className="bg-green-50 text-green-700 px-3 py-1 rounded-full text-sm font-medium border border-green-200">
                  ‚úÖ Verified
                </span>
              </div>
            </div>
          </div>
        </motion.div>

        {/* Stats Row */}
        <div className="grid grid-cols-2 lg:grid-cols-4 gap-4">
          {[
            { label: "Tasks Completed", value: "0", icon: Award, color: "text-green-600", bg: "bg-green-50" },
            { label: "Time Blocks", value: "0", icon: CalendarIcon, color: "text-blue-600", bg: "bg-blue-50" },
            { label: "Streak Days", value: "0", icon: Award, color: "text-orange-600", bg: "bg-orange-50" },
            { label: "Active Since", value: new Date(user.createdAt).toLocaleDateString('en-US', { month: 'short', year: 'numeric' }), icon: CalendarIcon, color: "text-purple-600", bg: "bg-purple-50" }
          ].map((stat, index) => (
            <motion.div
              key={stat.label}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: index * 0.1 }}
              whileHover={{ scale: 1.05, y: -4 }}
              className={`${stat.bg} rounded-xl p-4 border border-border shadow-md`}
            >
              <div className="flex items-center gap-2 mb-2">
                <stat.icon size={18} className={stat.color} />
              </div>
              <p className="text-2xl font-bold text-foreground mb-1">{stat.value}</p>
              <p className="text-xs text-muted-foreground">{stat.label}</p>
            </motion.div>
          ))}
        </div>

        {/* Settings Card */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.2 }}
          className="bg-card rounded-xl shadow-lg p-8 border border-border"
        >
          <div className="flex items-center justify-between mb-6">
            <h3 className="text-xl font-bold text-foreground flex items-center gap-2">
              <Edit2 size={20} />
              Preferences
            </h3>
            <motion.button
              onClick={() => setIsEditing(!isEditing)}
              whileHover={{ scale: 1.05 }}
              whileTap={{ scale: 0.95 }}
              className={`px-5 py-2 rounded-lg font-medium transition-all shadow-sm ${
                isEditing
                  ? "bg-destructive text-destructive-foreground"
                  : "bg-primary text-primary-foreground hover:bg-primary/90"
              }`}
            >
              {isEditing ? "Cancel" : "Edit Profile"}
            </motion.button>
          </div>

          <div className="space-y-5">
            {/* Bio */}
            <div>
              <label className="block text-sm font-semibold text-foreground mb-2">Bio</label>
              {isEditing ? (
                <textarea
                  value={bio}
                  onChange={(e) => setBio(e.target.value)}
                  placeholder="Tell us about yourself"
                  rows={4}
                  className="w-full px-4 py-3 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary resize-none transition-all"
                />
              ) : (
                <p className="text-foreground/80 bg-muted/30 px-4 py-3 rounded-lg">
                  {bio || "No bio added yet"}
                </p>
              )}
            </div>

            {/* Timezone */}
            <div>
              <label className="block text-sm font-semibold text-foreground mb-2">Timezone</label>
              {isEditing ? (
                <select
                  value={timezone}
                  onChange={(e) => setTimezone(e.target.value)}
                  className="w-full px-4 py-3 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary transition-all"
                >
                  <option>UTC</option>
                  <option>EST</option>
                  <option>CST</option>
                  <option>MST</option>
                  <option>PST</option>
                </select>
              ) : (
                <p className="text-foreground/80 bg-muted/30 px-4 py-3 rounded-lg">{timezone}</p>
              )}
            </div>

            {/* Work Hours */}
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-4">
              <div>
                <label className="block text-sm font-semibold text-foreground mb-2">Work Start Time</label>
                {isEditing ? (
                  <input
                    type="time"
                    value={workStartTime}
                    onChange={(e) => setWorkStartTime(e.target.value)}
                    className="w-full px-4 py-3 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary transition-all"
                  />
                ) : (
                  <p className="text-foreground/80 bg-muted/30 px-4 py-3 rounded-lg">{workStartTime}</p>
                )}
              </div>
              <div>
                <label className="block text-sm font-semibold text-foreground mb-2">Work End Time</label>
                {isEditing ? (
                  <input
                    type="time"
                    value={workEndTime}
                    onChange={(e) => setWorkEndTime(e.target.value)}
                    className="w-full px-4 py-3 border border-border rounded-lg bg-input text-foreground focus:outline-none focus:ring-2 focus:ring-primary transition-all"
                  />
                ) : (
                  <p className="text-foreground/80 bg-muted/30 px-4 py-3 rounded-lg">{workEndTime}</p>
                )}
              </div>
            </div>

            {/* Save Button */}
            <AnimatePresence>
              {isEditing && (
                <motion.button
                  initial={{ opacity: 0, y: -10 }}
                  animate={{ opacity: 1, y: 0 }}
                  exit={{ opacity: 0, y: -10 }}
                  onClick={handleSaveProfile}
                  whileHover={{ scale: 1.02 }}
                  whileTap={{ scale: 0.98 }}
                  className="w-full mt-6 bg-primary hover:bg-primary/90 text-primary-foreground px-6 py-3 rounded-lg font-medium transition-colors shadow-md"
                >
                  Save Changes
                </motion.button>
              )}
            </AnimatePresence>
          </div>
        </motion.div>

        {/* Danger Zone */}
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 0.3 }}
          className="bg-destructive/5 rounded-xl shadow-lg p-8 border-2 border-destructive/20"
        >
          <h3 className="text-xl font-bold text-destructive mb-4 flex items-center gap-2">
            ‚ö†Ô∏è Danger Zone
          </h3>
          <p className="text-destructive/80 mb-6 leading-relaxed">
            Logging out will end your current session. You can log back in anytime.
          </p>
          <motion.button
            onClick={handleLogout}
            whileHover={{ scale: 1.02 }}
            whileTap={{ scale: 0.98 }}
            className="flex items-center gap-2 bg-destructive hover:bg-destructive/90 text-destructive-foreground px-6 py-3 rounded-lg font-medium transition-colors shadow-md"
          >
            <LogOut size={20} />
            Logout from Account
          </motion.button>
        </motion.div>
      </div>
    </div>
  )
}
